* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Futura PT;
}

body {
  overflow: hidden;
}

html, body {
  width: 100%;
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.main {
  width: 100%;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.main.products-grid {
  background: #f7f8fa;
}

.main__container {
  width: 1140px;
}

.main__container.home {
  border-bottom: 3px solid silver;
  margin-bottom: 150px;
  padding-bottom: 120px;
}

.main__title {
  color: #000;
  margin-top: 85px;
  margin-bottom: 75px;
  font-family: Futura PT;
  font-size: 32px;
  font-style: normal;
  font-weight: 600;
  line-height: 41px;
}

.header {
  width: 100%;
  background-image: url("main-header-bg.a1961cde.jpg");
  background-position: 50% 0;
  background-repeat: no-repeat;
  background-size: cover;
  justify-content: center;
  align-items: center;
  padding: 20px 30px 250px;
  display: flex;
}

.header.products-header {
  background-image: url("product-bg.0dcf2e97.jpg");
}

.header.about-header {
  background-image: url("about-bg.7f89e1b6.jpg");
}

.header.contacts-header {
  background-image: url("contacts-header-bg.85b15095.jpg");
}

.header.product-item-header {
  background: #000;
  padding: 60px 30px;
}

.header__container {
  max-width: 1140px;
  width: 1140px;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.header__row {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__nav {
  margin-top: 0;
}

.header__nav .nav-menu {
  grid-gap: 75px;
  grid-auto-flow: column;
  display: grid;
}

.header__nav .nav-menu__item {
  padding-bottom: 10px;
  position: relative;
}

.header__nav .nav-menu__item.has-children > a:after {
  content: "";
  width: 12px;
  height: 7px;
  background-image: url("arrow-down.01274841.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  transition: all .3s;
  display: block;
  position: absolute;
  top: 50%;
  left: calc(100% + 5px);
}

.header__nav .nav-menu__item.has-children .sub-menu {
  visibility: hidden;
  opacity: 0;
  width: fit-content;
  min-width: 150px;
  background: #444;
  padding: 10px 25px 10px 10px;
  transition: all .3s;
  position: absolute;
  top: 100%;
  left: 0;
  transform: translateY(20px);
}

.header__nav .nav-menu__item.has-children:hover .sub-menu {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}

.header__nav .nav-menu__item.has-children:hover > a:after {
  transform: rotate(180deg);
}

.header__nav .nav-menu__item a {
  color: #fff;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 23px;
  display: inline-block;
  position: relative;
}

.header__logo {
  width: 152px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.header__logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  display: block;
}

.header__topbar {
  justify-content: flex-start;
  align-items: center;
  margin-top: 0;
  display: flex;
}

.header__topbar .contacts {
  color: #fff;
  border: 2px solid #fff;
  padding: 10px 55px;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
}

.header__topbar .edit, .header__topbar .search {
  margin-left: 25px;
}

.header__about {
  max-width: 650px;
  flex-direction: column;
  align-self: flex-start;
  align-items: flex-start;
  margin-top: 150px;
  display: flex;
}

.header__about .col {
  flex-direction: column;
  align-items: flex-end;
  display: flex;
}

.header__about .col .pr-title {
  letter-spacing: .19em;
  text-transform: uppercase;
  color: #fff;
  margin: 0;
  font-size: 48px;
  font-style: normal;
  font-weight: 600;
  line-height: 48px;
}

.header__about .col .sc-title {
  letter-spacing: .3em;
  text-transform: uppercase;
  color: #fff;
  text-align: right;
  font-size: 32px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
}

.header__about .content {
  color: #fff;
  margin-top: 40px;
  font-size: 18px;
  font-style: normal;
  font-weight: 300;
  line-height: 28px;
}

.header__about a {
  color: #fff;
  background: #b98500;
  margin-top: 20px;
  padding: 15px 35px;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 21px;
}

.header__features {
  flex-direction: column;
  justify-content: flex-start;
  align-self: flex-end;
  align-items: flex-end;
  margin-top: 15px;
  display: flex;
}

.header__features .th-title {
  letter-spacing: .2em;
  color: #fff;
  font-size: 24px;
  font-style: normal;
  font-weight: 200;
  line-height: 31px;
}

.header__features ul {
  margin-top: 30px;
  display: flex;
}

.header__features ul li {
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  display: flex;
}

.header__features ul li + li {
  margin-left: 100px;
}

.header__features ul li h4 {
  letter-spacing: .1em;
  color: #fff;
  font-size: 36px;
  font-style: normal;
  font-weight: 400;
  line-height: 44px;
}

.header__features ul li span {
  letter-spacing: .1em;
  color: #fff;
  margin-top: 5px;
  font-size: 16px;
  font-style: normal;
  font-weight: 200;
  line-height: 21px;
}

.header__title {
  letter-spacing: .19em;
  text-transform: uppercase;
  color: #fff;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  margin-top: 150px;
  font-family: Futura PT;
  font-size: 48px;
  font-style: normal;
  font-weight: 600;
  line-height: 62px;
  display: flex;
}

.products {
  width: 100%;
  padding-top: 150px;
  position: relative;
}

.products:before {
  content: "";
  width: 100vw;
  height: 100%;
  background: #fff;
  display: block;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%);
}

.products__container {
  width: 100%;
}

.products__title {
  color: #000;
  margin-bottom: 25px;
  font-family: Futura PT;
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: 31px;
}

.products__grid {
  grid-gap: 25px 30px;
  background: #fff;
  grid-template-columns: repeat(4, 260px);
  display: grid;
}

